

const expensesReducerDefaultState = []

const expensesReducer = (state= expensesReducerDefaultState, action) => {
    switch (action.type) {
        case 'ADD_EXPENSE':

            //return  state.concat(action.expense)
            return [
                ...state, //Burada state aslnda bir array, action ise bir nesne ve nesnemiz bir adet yaz yani type ve bir adet nesneden oluuyor. Yani expense. Burada state array' nesnelerden olutuu iin ierisinine bu ekilde yeni nesnemizi ekliyoruz.
                action.expense
            ]
            

        case 'REMOVE_EXPENSE':

            return state.filter(({id})=>id !== action.id);    

            /* Bu Benim yaptm ve skntsz alt.
            const newState= state.filter((exp)=>{
                return exp.id !== action.id
            })
            
            return newState; */
        case 'EDIT_EXPENSE':
            
            return state.map((expense)=>{
                if(expense.id === action.id){
                    return {
                        ...expense,
                        ...action.updates
                    }
                }else{
                    return expense;
                }
            });

        default:
            return state;
    }
}

export default expensesReducer;